.App {
  text-align: center;
  display:grid;
  place-items: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.items {
  display: grid;
  grid-auto-flow: column;
  gap:2vh 2vw;
  max-width: 100vw;
}

.item {
  display:grid;
  place-items: center;
  padding:2vh 2vw;
  border:2px solid black;
  border-radius: 10px;
}
.item input {
  width:50%;
}
.item img {
  width:15vw;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}



.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
